function blockHeight(block,wWidth){
	
	var block = $(block); 
	var defaults = 495; 
	if(wWidth){ 
		minWidth = wWidth-17 
	}else{ 
		minWidth = defaults 
	} 
	
	if($(window).width()>minWidth){ 
		var maxH = 0;
		console.log(1)
		block.each(function(){
			if($(this).height()>maxH){ 
				maxH = $(this).height() 
			} 
		}) 
		block.height(maxH) 
	}else{
		console.log(2)
		block.css('height','auto') 
	} 
	$(window).resize(function(){ 
		if($(window).width()>minWidth){ 
			var maxH = 0; 
			block.each(function(){ 
				$(this).css('height','auto') 
				if($(this).height()>maxH){ 
					maxH = $(this).height() 
				} 
			}) 
			block.height(maxH) 
		}else{ 
			block.css('height','auto') 
		} 
	}) 
}
