$('.search-select__current').click(function(){ 
	$('.search-select__list').not($(this).siblings('.search-select__list')).fadeOut(); 
	$('.search-select').not($(this).parents('.search-select')).removeClass('search-select_active'); 
	$(this).siblings('.search-select__list').fadeToggle(); 
	$(this).parents('.search-select').toggleClass('search-select_active'); 
}); 

$('.search-select__item').click(function(){ 
	var text = $(this).text(), 
	val = $(this).attr('data-val'); 
	$(this).parents('.search-select').find('.search-select__current').text(text).attr('data-val', val); 
	$(this).parents('.search-select__list').fadeToggle(); 
	$(this).parents('.search-select').toggleClass('search-select_active'); 
});