function collapse(){
	if($(window).width() < 991){
		$('.left-sidebar-list').fadeToggle('left-sidebar-list--hidden');
	}
}

$('.content__title--collapse').on('click', function(){
	collapse();
})

$('.left-sidebar-list__item > a').on('click', function(e){
	e.preventDefault();
	$(this).toggleClass('open');
	$(this).next().fadeToggle();
})