$('.tovar-image').fancybox();

function inputVal(a, b){
	var plus = $('#plus'),
	minus = $('#minus'),
	value = '';

	plus.on('click', function(){
		value = parseInt($('#inp-result').val());
		if(value < a){
			value = value + 1;
			$('#inp-result').val(value);
		}
	})

	minus.on('click', function(){
		value = parseInt($('#inp-result').val());
		if(value > b){
			value = value - 1;
			$('#inp-result').val(value);
		}
	})
}
inputVal(99, 1);